import React from 'react';
import Styled from 'styled-components';

const Container = Styled.div`
    width: 100%;
    height: fit-content;
    padding-bottom: 1em;

    background-color: #0d0d0d;

`;

const Title = Styled.h2`
    text-align: center;
    color: #ffffff;
    font-size: 1.5em;
    padding: 0.5em 0;
    margin: 0;
    margin: 0;
`;

const ContentContainer = Styled.div`
    width: auto;
    
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(min(200px,95vmin), 1fr));
    grid-gap: 0.5em 2em;
    place-items: center;
    justify-content: center;

`;

const Content = Styled.div`
    color: #ffffff;
    margin: 0 1em;
    fill: #ffffff;

    a{
        text-decoration: none;
        color: inherit;
        margin: 0;
        padding: 0;
    }

    svg{
        padding-top: 0.25em;
        padding-right: 0.25em;

        fill: inherit;
    }

    &:hover{
        color: #FF4500;
        fill: #FF4500;
    }
`;

const DownArrow = Styled.div`
    width: 100%;
    height: 10em;   
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;


    svg{
        fill: #FF4500;

        &:hover{
            fill: white;
        }
    }
`;

